import Vue from 'vue'
import Router from 'vue-router'
import home from './home.js'
import cartoon from './cartoon.js'
import hobby from './hobby.js'
import user from './user.js'

Vue.use(Router)

let routes = []
routes = routes.concat(home)
routes = routes.concat(cartoon)
routes = routes.concat(hobby)
routes = routes.concat(user)

// for (let i = 0, j = routes.length; i < j; i++) {
//   routes[i].name += '.v1'
// }

export default new Router({
  mode: 'history',
  routes: routes
}) 


