export default[
  {
    path: '/cartoon/index.html',
    name: '10001',
    title: '漫画列表',
    keywords: '大是伟大的大，虫是懒虫的虫，大虫漫画',
    description: '高质量漫画社交生活平台，大虫漫画',
    component: (resolve) => { require(['@/view/cartoon/main/tpl'], resolve) }
  }, {
    path: '/cartoon/detail/index.html',
    name: '10002',
    title: '漫画详情',
    component: (resolve) => { require(['@/view/cartoon/detail/tpl'], resolve) }
  }
]


